.statistic-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 12px 0;
  border: 1px solid #ddd;
  border-radius: 5px;

  li {
    width: 33%;
    display: flex;
    flex-direction: column;
    list-style: none;
    text-align: center;
    border-right: 1px solid #ddd;

    &:last-child {
      border-right: none;
    }

    & > .statistic-top__value {
      font-size: 40px;
    }
  }
}
