@import '~antd/dist/antd.less';

body {
  margin: 0;
  height: auto;
}

.ant-form {
  .ant-form-item {
    margin-bottom: 16px;

    .ant-form-item-label {
      display: block;
      width: 100%;
      text-align: left;

      & label {
        color: rgba(0,0,0,0.6);
      }
    }
  }
}

.ant-form-horizontal .ant-form-item-control {
  min-width: auto;
}
